* {
    padding: 0;
    margin: 0;
    border: 0;
}
*,
*:before,
*:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
:focus,
:active {
    outline: none;
}
a:focus,
a:active {
    outline: none;
}
nav,
footer,
header,
aside {
    display: block;
}
html,
body {
    height: 100%;
    width: 100%;
    font-size: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
    font-family: inherit;
}
input::-ms-clear {
    display: none;
}
button {
    cursor: pointer;
}
button::-moz-focus-inner {
    padding: 0;
    border: 0;
}
a,
a:visited {
    text-decoration: none;
}
a:hover {
    text-decoration: none;
}
ul li {
    list-style: none;
}
img {
    vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: inherit;
}
body {
    background: #ffffff;
    font-family: "Roboto";
    font-size: 18px;
}
#root {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.wrapper {
    width: 100%;
    margin-top: -100px;
    background: #ffffff;
    max-width: 620px;
    min-height: 460px;
    padding: 20px;
    box-sizing: content-box;
}
.search-bar {
    position: relative;
    padding: 0px 15px;
    border: 1px solid black;
    border-bottom: none;
}
.search-bar__input {
    height: 45px;
    width: 90%;
    font-size: 18px;
}
.search-bar__btn {
    background-color: transparent;
    position: absolute;
    right: 10px;
    top: 10px;
}
.icon-btn:hover {
    cursor: pointer;
    user-select: none;
    opacity: 0.6;
}

.info {
    height: 390px;
    width: 100%;
    display: flex;
}
.info-block {
    flex: 1 1 50%;
}
.left-info {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
}
.right-info {
    border: 1px solid black;
    border-left: none;
}

.left-info__screen {
    width: 100%;
    padding: 16px;
    position: relative;
    height: 385px;
    display: block;
    overflow-y: auto;
}
.left-info__screen.error {
    font-size: 20px;
    text-align: center;
    color: red;
    display: flex;
    justify-content: center;
    align-items: center;
}
.left-info__screen::-webkit-scrollbar {
    height: 12px;
    background-color: #ffffff;
    width: 8px;
}
.left-info__screen::-webkit-scrollbar-thumb {
    background-color: #4b4848;
}
.now__weather-icon {
    position: absolute;
    width: 170px;
    height: 170px;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
}
.now__icon-heart {
    background-color: transparent;
    position: absolute;
    right: 26px;
    bottom: 23px;
    cursor: pointer;
}
.now__temperature {
    font-size: 70px;
    font-weight: 400;
}
.city-name {
    font-size: 34px;
    font-weight: 400;
}
.now__city-name {
    position: absolute;
    max-width: 210px;
    left: 17px;
    bottom: 23px;
}
.detalis__city-name {
    margin-bottom: 25px;
}
li {
    list-style-type: none;
    margin-bottom: 15px;
    position: relative;
}
.locations__list li {
    font-size: 26px;
}
.detalis__list li {
    font-size: 26px;
}
.forecast__city-name {
    margin-bottom: 15px;
}
.buttons {
    border-top: 1px solid black;
    display: flex;
    height: 45px;
}
.btn.tab {
    flex: 1 1 33.333%;
    line-height: 45px;
    text-align: center;
    background: inherit;
    font-size: 18px;
    transition: all 0.1s ease-in-out;
}
.btn.tab:not(:last-child) {
    border-right: 1px solid black;
}
.btn.tab:hover {
    background-color: #4b4848;
    color: #ffffff;
    cursor: pointer;
    user-select: none;
}
.btn.tab.active {
    color: #ffffff;
    background: #000000;
}
.now__icon-heart.active {
    opacity: 0.4;
}
.locations__name {
    min-height: 68px;
    font-size: 28px;
    line-height: 68px;
    padding: 0px 20px;
    border-bottom: 1px solid black;
}
.right-info__locations {
    display: flex;
    flex-direction: column;
}
.locations__list {
    padding: 7% 5% 1% 5%;
    overflow-y: scroll;
    scroll-behavior: smooth;
    height: 319px;
}
.locations__list * {
    cursor: pointer;
    user-select: none;
}
.locations__list::-webkit-scrollbar {
    height: 10px;
    background-color: #ffffff;
    width: 8px;
}
.locations__list::-webkit-scrollbar-thumb {
    background-color: #4b4848;
}
.forecast__list-weeks {
    display: flex;
    flex-direction: column;
}
.forecast__wrapper {
    margin-bottom: 30px;
}
.week {
    padding: 12px;
    border: 1px solid black;
    height: 120px;
    width: 100%;
    margin-bottom: 9px;
    position: relative;
}
.week__row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 33px;
}
.icon-weather__forecast {
    position: absolute;
    right: 5px;
    bottom: -5px;
    width: 60px;
    height: 60px;
}
.locations-item__btn {
    background-color: transparent;
    position: absolute;
    top: -2px;
    right: 3px;
    font-size: 30px;
}
.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
}
.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid black;
    border-color: black transparent black transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@media screen and (max-width: 767px) {
    .left-info {
        flex: 1 1 100%;
    }
    .right-info {
        width: 100%;
        border-left: 1px solid black;
        border-top: none;
    }
    .info {
        flex-direction: column;
    }
}
